.meals > .logo {
  animation: shake 1.5s infinite ease;
  color: aliceblue;
  font-family: Montserrat , sans-serif;
  font-size: 3em;
  font-weight: 900;
}

@keyframes shake {

  0% {
    transform: rotate(5deg);
  }

  50% {
    transform: rotate(-5deg);
  }

  100% {
    transform: rotate(5deg);
  }
}

@keyframes clink {

  0% {
    transform: rotate(-10deg);
  }

  50% {
    transform: rotate(20deg);
  }

  100% {
    transform: rotate(-10deg);
  }
}

.start-recipe-btn {
  bottom: 0;
  position: fixed;
}

.recipeImg {
  height: 100px;
  width: 100px;
}

.horizontal-name {
  width: 80%;
}

.texto-riscado {
  text-decoration: line-through;
}

.share-btn {
  border-radius: 10px;
}
